import { Form, Input, Button } from 'antd';
import { observer } from 'mobx-react';
import { publishStore } from '../../stores/PublishStore';
import { userStore } from '../../stores/UserStore';
import './Apply.scss';
const FormItem = Form.Item;
const { TextArea } = Input;

const txtPlaceHolder = `1. 
2. `;

export const ApplyPage = observer(() => {

  const doSubmit = (values: any) => {
    publishStore.apply(values);
  };
  return (
    <div className="creator-apply-page">
      <h1>Oli</h1>
      {(userStore.userInfo?.applyCreatorStatus == 0 || publishStore.reviewing) && <div className="success-view">
        <h2></h2>
        <div className="success-desc"></div>
      </div>}
      {userStore.userInfo?.applyCreatorStatus == -1 && <Form className="form" labelAlign="left" onFinish={doSubmit}>
        <FormItem label="" name="name" required>
          <Input placeholder="210" />
        </FormItem>
        <FormItem label="" name="email" required>
          <Input placeholder="" />
        </FormItem>
        <FormItem label="" name="phone" required>
          <Input placeholder="" />
        </FormItem>
        <FormItem label="" name="describe" required>
          <TextArea placeholder={txtPlaceHolder} />
        </FormItem>
        <FormItem label=" ">
          <Button htmlType="submit" loading={publishStore.loading} className="submit-btn"></Button>
        </FormItem>
      </Form>}
    </div>
  )
});